import * as analytics from '../analytics'
import fetcher from '../utils/fetcher'

const loginSubmit = ({ history }) => (values, { setSubmitting, setFieldError, setErrors, ...rest }) => {
  analytics.loginSubmit()

  return fetcher('/login', {
    method: 'POST',
    body: values,
  })
  .then((res) => {
    setSubmitting(false)

    if (res.errors) {
      analytics.loginFail(res.errors)
      setTimeout(() => setErrors({}), 2000)
      return setFieldError('form', res.errors.message)
    }

    /* Do Analytics Logic */
    analytics.loginSuccess(res)
    
    /* Do Authentication Logic */
    localStorage.setItem('userName', res.email)
    localStorage.setItem('auth', JSON.stringify(res))
    
    /* Do Routing Logic */
    history.push('/dashboard')
  })
}

export default loginSubmit


