import qs from 'query-string'
import { API_URL } from '../config'

const extractData = res => res.json()

const headers = { 'Content-Type': 'application/json' }

const defaultConfig = ({
  withAuth = false,
}) => ({
  method: 'GET',
  headers: Object.assign(
    headers,
    withAuth && { 'Authorization': `Token ${(JSON.parse(localStorage.getItem('auth')) || {}).token}` },
  ),
})

/*
 * Optional Parameters:
 * - sendAuth:boolean:false
 */

const fetcher = (endpoint, { query, sendAuth, ...configParam }) => {
  let q = query ? qs.stringify(query) : ''
  let config = Object.assign(
    {},
    defaultConfig({ sendAuth }),
    configParam,
    configParam.body && { body: JSON.stringify(configParam.body) },
  )

  return fetch(API_URL + endpoint + q, config)
    .then(extractData)
}

export default fetcher


