import ReactGA from 'react-ga'
import getUserId from './utils/getUserId'
import { TEST_MODE } from './config'

const userId = getUserId()

const gaConfig = {
  debug: TEST_MODE,
  testMode: TEST_MODE,
  titleCase: false,
  gaOptions: Object.assign({}, userId && { userId }),
}

ReactGA.initialize('UA-112881700-1', gaConfig)

export const signupSubmit = () => {
  ReactGA.event({
    category: 'Login and Signup',
    action: 'Signup Submit',
    label: 'Local',
    dimension3: 'Local',
    metric3: 1,
  })
}

export const signupSuccess = (user) => {
  ReactGA.set({ userId: user._id })
  ReactGA.event({
    category: 'Login and Signup',
    action: 'Signup Success',
    label: 'Local',
    dimension1: user._id,
    dimension2: 'Yes',
    dimension3: 'Local',
    metric1: 1,
  })
}

export const signupFail = (err) => {
  ReactGA.event({
    category: 'Login and Signup',
    action: 'Signup Fail',
    label: `Local: ${err.message}`,
    dimension3: 'Local',
    metric2: 1,
  })
}

export const loginSubmit = () => {
  ReactGA.event({
    category: 'Login and Signup',
    action: 'Login Submit',
    label: 'Local',
    dimension3: 'Local',
    metric6: 1,
  })
}

export const loginSuccess = (user) => {
  ReactGA.set({ userId: user._id })
  ReactGA.event({
    category: 'Login and Signup',
    action: 'Login Success',
    label: 'Local',
    dimension1: user._id,
    dimension2: 'Yes',
    dimension3: 'Local',
    metric4: 1,
  })
}

export const loginFail = (err) => {
  ReactGA.event({
    category: 'Login and Signup',
    action: 'Login Fail',
    label: `Local: ${err.message}`,
    dimension3: 'Local',
    metric5: 1,
  })
}

// Modals

export const addPropertyModal = () => ReactGA.modalview('Add Property')

export const addApplicationModal = () => ReactGA.modalview('Add Application')

export default ReactGA



